    .button__navigation.-inactive, .button__navigation.-inactive:hover {
      background-color: initial;
      cursor: initial; }
      .button__navigation.-inactive::after, .button__navigation.-inactive:hover::after {
        color: rgba(51, 51, 51, 0.5); }

